@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,600,600i,700,700i,800,800i&display=swap&subset=cyrillic,cyrillic-ext');

.movieItem {
    margin-bottom: 20px;
    font-family: 'Open Sans', sans-serif;
    background: #fff;
    border-bottom: 1px solid #bdbdbd;
}

.movieItem__imgBlock {
    max-height: 210px;
    overflow: hidden;
    margin-bottom: 20px;
    position: relative;
    background: linear-gradient(to top right, #21222a, #a7accf);
}

.movieItem img {
    width: 100%;
    height: 100%;
    opacity: .5;
}

.movieItem .movieItem__title {
    font-weight: bold;
    font-size: 27px;
    padding: 15px;
    position: absolute;
    bottom: 0;
    color: #fff;
}

.movieItem .movieItem__desc {
    padding: 15px;
    line-height: 30px;
    font-weight: 100;
    width: 90%;
    font-size: 16px;
    color: #9E9E9E;
}